import { Color } from './color.js'

import './colorList.scss'

export const ColorList = ({ colors, starLimit, onRate, onRemove }) => {
  const colorList = colors.map((color, i) => {
    return (
      <Color key={i}
        colorInfo={color}
        starLimit={starLimit}
        onRate={() => onRate()}
        onRemove={() => onRemove()} />
    )
  })
  return (
    <div className="color-list u-flex--item-table">
      { colors.map((color, i) => {
          <Color key={i}
            colorInfo={color}
            starLimit={starLimit}
            onRate={() => onRate()}
            onRemove={() => onRemove()} />
        })
      }
    </div>
  )
}



